.confirming-delete {
  overflow: hidden;
}

body {
  --col1: blue;
  --col2: rgb(236, 238, 250);
  --col3: rgb(245, 245, 247);
  --good: green;
  --bad: red;
  --light: white;
  --dark: black;

  --max-width: 900px;

  font-family: 'Ubuntu', sans-serif;

  background-color: var(--col2);
}

.wrapper {
  min-height: 100vh;
  overflow-x: hidden;
  display: grid;

  grid-template-rows: auto 1fr;
}

#top {
  background-color: var(--col1);
  color: var(--light);

  position: sticky;

  height: 80px;

  max-height: 10vh;

  display: grid;
  align-items: center;
  justify-items: center;
}

#top>* {
  width: 90vw;
  max-width: var(--max-width);

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "back insert";
}

#top>* .back-btn {
  grid-area: back
}
#top>* .new-btn {
  grid-area: insert
}

#top button, #contact-list button, .form-component button {
  color: inherit;
  background-color: transparent;
  border: none;
}

main {
  display: grid;
  justify-content: center;
  align-self: stretch;
}

#main-div {
  width: 90vw;
  max-width: var(--max-width);
  background-color: var(--light);
}

#contact-list, .form-component ul {
  list-style: none;
  padding: 0;

  display: grid;
}

#contact-list {
  font-size: 20px;
  margin: 0;
}

#contact-list>* {
  height: 3em;
  padding: 20px;

  display: grid;
  grid-template-columns: 9fr 1fr;
  align-items: center;

  border-bottom: 1px solid var(--col2);

  background-color: transparent;

  transition: 0.2s background-color;
}

#contact-list button {
  color: var(--bad);
}

#contact-list>*:hover {
  background-color: var(--col3);
}

.form-component {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.form-component>* {
  margin: 10px;
  min-height: 2em;
}

.form-component label {
  text-align: end;
}

.form-component label::after {
  content: ":";
}

.validatable {
  display: grid;
}

.editable {
  display: grid;
  grid-template-columns: 9fr 1fr;
}

.editable i {
  color: var(--col1) !important;
}

.save-load-btns {
  display: grid;
  margin-top: 2em !important;
  height: auto !important;
}

button.submit-btn, .save-load-btns {
  justify-self: stretch !important;
  justify-items: stretch !important;
}

.save-load-btns>*, button.submit-btn {
  margin-top: 10px;
  border-radius: 10px;
}

.save-load-btns>:first-child, button.submit-btn {
  border: 2px solid var(--good);
  color: var(--good);
}

.save-load-btns>:nth-child(2) {
  border: 2px solid var(--col1);
  color: var(--col1);
}

button.delete-btn {
  color: var(--bad);
  margin-top: 3em;
  margin-right: 3em;
  justify-self: end;
}

.phones button {
  width: 100%;
  text-align: start;
}

.phones button:hover {
  color: var(--col1);
}

.phones li {
  height: 2em;
}

.save-load-btns, .delete-btn, .submit-btn {
  grid-column: 1 / -1;
  justify-self: center;
  align-self: center;
}

#delete-dlg {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  align-items: center;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

#delete-dlg>section {
  text-align: center;
  display: grid;
  grid-template-areas: "msg msg";

  min-height: 20vh;

  background-color: var(--bad);
  color: var(--light);
}

#delete-dlg>section>:first-child {
  grid-area: msg;
  align-self: center;
  font-size: 32px;
  font-weight: bold;
}

#delete-dlg>section>:not(:first-child) {
  margin: 20px;
  border: none;
  background-color: var(--light);
}

.valid {
  color: var(--good);
}

.invalid {
  color: var(--bad);
}

.valid input {
  outline: var(--good) 1px solid;
}

.invalid input {
  outline: var(--bad) 1px solid;
}